@import url(https://fonts.googleapis.com/css?family=Oswald);
body {
  margin: 0;
  font-family: 'Roboto','RobotoDraft','Helvetica','Arial','sans-serif',
    'sans-serif';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F2F3F3;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.topNavBar{
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  /* justify-content: center; */
  -webkit-align-items: center;
          align-items: center;
  padding-top: 30px;
  padding-left: 15px;
}

.logo{
  /* padding-left: 15px; */
  position: absolute;
  text-align: center;
  font-size: 30px;
  text-decoration: none;
  font-weight: bold;
  color:black;
  letter-spacing: -0.15em;
  vertical-align: text-top;
}

.tabItem{
  text-align: center;
  padding: 20px;
  text-decoration: none;
  color:black;
}

.tabItem:hover{
  text-decoration: underline;
  -webkit-text-decoration-style: double;
          text-decoration-style: double;
}

.diviver {
  display:-webkit-flex;
  display:flex;
}

.solid {
  margin: 0;
  margin-bottom: 10px;
}
.button {
  text-align: center;
  -webkit-align-items: center;
          align-items: center;
  margin-left: 5px;
  margin-right: 5px;
  padding: 5px;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: #fbbc04;
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  border-radius: 5px;
}

.label {
  -webkit-align-self: center;
          align-self: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-weight: bold;
  /* font-size: 20px; */
}
.card {
  /* border: 1px solid gray; */
  background-color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly ;
          justify-content: space-evenly ;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 5px;
  padding: 10px;
  border-radius: 5px;
}

span {
  
}
.app {
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.menu {
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.input {
  border: 0px;
  width: 300px;
  height: 35px;
}

.subheading {
  width: 614px;
  padding-right: 200px;
  text-align: left;
  /* margin-top: 50px; */
  margin-bottom: 50px;
}


@media print {
  .pagebreak {
    page-break-before: always;
  }
}

@media (prefers-reduced-motion: no-preference) {
.app h1 {
    text-align: left;
    /* animation: App-logo-float infinite 3s ease-in-out; */
    font-size: 60px;
    width: 614px;
    padding-right: 200px;
    font-weight: 100;
  }
}
.App-header {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: rgb(112, 76, 182);
}

@-webkit-keyframes App-logo-float {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px)
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px)
  }
}

@keyframes App-logo-float {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px)
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px)
  }
}
.wrapper {
  height: 110px;
  background-color: whitesmoke;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: 'center';
          align-items: 'center';
  overflow-x: scroll;
}

.progressBar {
  height: 5px;
  width: 100%;
  background-color:lightgray;
}
.TimeProgressNode {
  height: 5px;
  background-color:indianred;
}
.ExtractProgressNode {
  height: 5px;
  background-color: #1d8102;
}

.item {
  
  height: 90px; 
  border: 1px solid gainsboro;
  border-width: 1;
  min-width: 10px;
  text-align: center;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.item .view{
  text-align: center;
  height: 70%;
}

.item .remove{
  text-align: center;
  height: 30%;
  background-color:indianred;
}
.item .none{
  text-align: center;
  height: 20%;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
::-webkit-scrollbar-thumb {
  background: #888; 
}

::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.controlPane {
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: row ;
          flex-direction: row ;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-left: 20px;
  margin-right: 20px;
}

.controlPane .controlButtons {
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction:row;
          flex-direction:row;
}

.controlPane .controlButtons button{
  height: 30px;
  width: 30px;
  margin-left: 5px;
  margin-right: 5px;
}
